.project{
    background-color: #191919;
   
}
img.project__img {
    border-radius: 10px;
    width: 100%;
    object-fit: cover;
    transition: .5s;
    opacity: .7;
    position: relative;
}
img.project__img::after{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 1;
    opacity: 1;
}
.mask__effect{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
    transition: .5s;
    opacity: .7;
    
}
.project__box:hover .mask__effect{
    display: inline;
    background-color: #000000;
    border-radius: 10px;
 }
 .project__meta.absolute {
    top: 0%;
    left: 0%;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    border-radius: 10px;
    width: 100%;
}

.project__card{
    margin-top: 50px;
    margin-left: 4.7rem;
    display: inline-flex;
    max-width: 27%;
}
h5.project__text {
    font-weight: 900;
    font-size: 20px;
    padding-bottom: 20px;
    color: #ff8400;
}
h4.project__text {
    font-size: 22px;
    font-family: Poppins,sans-serif;
    font-weight: 700;
    padding-bottom: 20px;
    color: #ff8400;
}
a.project__btn {
    padding: 12px 35px;
    border: 2px solid #ffffff87;
    border-radius: 5px;
    transition: .4s ease;
    font-weight: 500;
}
a.project__btn:hover{
    background-image:  linear-gradient( 
        90deg,#1d1d1d,#ff8400);
    border: 2px solid #191919;
    color: #101010;
    
    
}
button.btn {
    padding: 15px 20px;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    border: 2px solid #ff8400;
    background-color: #ff8400;
    color: #fff!important;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .4s all ease;
}
button.view__more:hover{
    background-color: #fff;
    border: 2px solid #ff8400;
    color: #ff8400!important;
}
h1{
    color: #ff8400;
}
/* RESPONSIVE */
@media only screen and (max-width: 1024px){
h5.project__text {
    font-weight: 400;
    font-size: 20px;
    padding-bottom: 20px;
}
h4.project__text {
    font-size: 18px;
}
.project__card{
    margin-top: 55px;
    margin-left: -.5rem;
    display: inline-flex; 
    min-width: 34%;
    padding: 10px;
}
}

@media screen and (max-width: 980px) {
    h5.project__text {
        font-weight: 400;
        font-size: 20px;
        padding-bottom: 20px;
    }
    h4.project__text {
        font-size: 18px;
    }
    .project__card{
        display: inline-block;
        margin-top: 55px;
        margin-left: -.5rem;
        min-width: 34%;
        padding: 10px;
    }
}

@media  screen and (max-width: 590px) {
    h5.project__text {
        font-weight: 400;
        font-size: 20px;
        padding-bottom: 20px;
        
    }
    h4.project__text {
        font-size: 18px;
        text-align: center;
    }
    .project__card{
        display: inline;
    }
}